import { Component, useState } from 'react';
import './App.css';
import Title from './Components/Title';
import Control from './Components/Control';

class App2 extends Component {
  constructor(props) {
    super(props);
    this.state = {
      datas: [],
      dataItemChilds: [],
      title: '',
      keyword: '',
      sort: {
        by: 'price',
        value: 1
      },
    }
  }

  componentWillMount() {
    this.getData();
  }
  getAllData(filter, sort) {

  }
  getData(from = null, to = null) {
    fetch(`./Files/data.json`)
      .then(response => response.json())
      .then(res => {
        var datas = res.filter((e) => (from == null || e.price >= from) && (to == null || e.price <= to));
        localStorage.setItem("dataItemChilds", JSON.stringify(datas));
        this.setState({
          datas: datas
        });
      });
  }

  onSort = (sortBy, sortValue) => {
    console.log("aaaaafewfwgerg", sortBy, sortValue);
    this.setState({
      sortBy: sortBy,
      sortValue: sortValue
    });
  }

  onSearch = (keyword) => {
    this.setState({
      keyword: keyword
    });
  }
  render() {
    var {
      sortBy,
      sortValue,
      keyword
    } = this.state;
    if (sortBy === 'price') {
      this.state.datas.sort((a, b) => {
        if (a.price > b.price) return sortValue;
        else if (a.price < b.price) return -sortValue;
        else return 0
      })
    }
    else if (sortBy === 'accountnr') {
      this.state.datas.sort((a, b) => {
        if (a.name > b.name) return sortValue;
        else if (a.name < b.name) return -sortValue;
        else return 0
      })
    }

    if (keyword) {
      this.state.datas = this.state.datas.filter((data) => {
        return data.name.toLowerCase().indexOf(keyword) !== -1;
      });
    }
    else {
      
    }

    var elmArray = this.state.datas.sort(e => e.price).map((x, index) => {
      let background = '';
      //if (x.id != null) {
      if (x.price <= 1000000) {
        background = "./Files/images/h1.png"
      }
      else if (x.price > 1000000 && x.price < 5000000) {
        background = "./Files/images/h2.jpg"
      }
      else if (x.price > 5000000 && x.price < 10000000) {
        background = "./Files/images/h3.png"
      }
      else {
        background = "./Files/images/h4.png"
      }

      const style = {
        background: `url("${background}") center center / cover`,
      };
      return (
        <div className=" col-4" key={index}>
          <div className="module mid" style={style}>
            <h2 className="pt-5">{x.name}
              <br />
              {x.price}
            </h2>
          </div>
        </div>
      );
      //}
    });
    return (
      <div>
        <div className="header">
          <h1>Tài khoản số đẹp MB</h1>
        </div>
        <div className="row">
        </div>
        <div className="row">
          <div className="col-3 col-s-3 menu">
            <ul>
              <li>Mức giá</li>
              <li onClick={() => this.getData(0, 1000000000000000)}>Tất cả giá</li>
              <li onClick={() => this.getData(0, 1000000)}>0 - 1.000.000</li>
              <li onClick={() => this.getData(1000000, 5000000)}>1.000.000 - 5.000.000</li>
              <li onClick={() => this.getData(5000000, 10000000)}>5.000.000 - 10.000.000</li>
              <li onClick={() => this.getData(10000000, 1000000000000000)}>Hơn 10.000.000</li>
            </ul>
          </div>
          {/* ///title */}
          <div className="col-6 col-s-9">
            <Control
              onSearch={this.onSearch}
              onSort={this.onSort}
              sortBy={sortBy}
              sortValue={sortValue}
            />
            <Title />
            <div className="row">
              {elmArray}
            </div>
          </div>
          <div className="col-3 col-s-12">
            <div className="aside">
              <h2>What?</h2>
              <p>Chania is a city on the island of Crete.</p>
              <h2>Where?</h2>
              <p>Crete is a Greek island in the Mediterranean Sea.</p>
              <h2>How?</h2>
              <p>You can reach Chania airport from all over Europe.</p>
            </div>
          </div>
        </div>
        <div className="footer">
          <p>Resize the browser window to see how the content respond to the resizing.</p>
        </div>
      </div>
    );
  }
}

export default App2;
